import React from 'react'
import Scrollbars from 'react-custom-scrollbars-2';
import { useSelector } from 'react-redux'
import ActivityTimeline from '../../shared/Timeline/ActivityTimeline';

export default function LoanTimelinePage() {

    const loansRedux = useSelector((state) => state.loans.value);
    const loan = loansRedux?.selectedLoan;
    const timeline = JSON.parse(loan?.data?.timeline);
    return (
        <div>
            <Scrollbars style={{ overflowX: 'hidden', width: '100%', height: 370, backgroundColor: '#fff' }}>
                <ActivityTimeline items={timeline} />
            </Scrollbars>
        </div>
    )
}
