import React from 'react';
// Redux
import { useSelector } from 'react-redux';
// MUI Components
import { Box, Paper, Tab, Tabs, Grid, Typography, Divider } from '@mui/material';
import { makeStyles } from '@mui/styles';
// Page Components
import TabPanel from '../../tabs/components/TabPanel3';
import utilityService from '../../../../services/utils/utilityServices';

const useStyles = makeStyles({
    rowsTitleFirst: {
        border: '1px solid #000',
        fontWeight: 'bolder',
        textAlign: 'left',
        padding: 5,
    },
    rowsContentFirst: {
        border: '1px solid #000',
        padding: '5px',
        paddingLeft: '15px'
    },
    rowsTitle: {
        border: '1px solid #000',
        borderLeft: 'none',
        fontWeight: 'bolder',
        textAlign: 'center',
        padding: 5,
    },
    rowsContent: {
        border: '1px solid #000',
        borderLeft: 'none',
        borderTop: 'none',
        padding: '5px',
        paddingLeft: '15px',
        textAlign: 'center',

    },
})

export default function CashflowDetailsPage(props) {

    const loanRedux = useSelector((state) => state.loans.value);

    const selectedLoan = loanRedux.selectedLoan;
    const classes = useStyles(props);

    const cashflow = selectedLoan.data;

    const [value, setValue] = React.useState(0);
    const handleChange = (value) => {
        setValue(value);
    }

    let totalExpense = 0.0;
    let totalIncome = 0.0;

    return (
        <div>
            <Paper square sx={{ border: "1px solid #00BFA5", p: 1 }}>
                <Tabs
                    value={value}
                    onChange={handleChange}
                    sx={{ backgroundColor: 'primary' }}
                >
                    <Tab onClick={() => handleChange(0)} sx={{ alignItems: "start" }} label={
                        <Typography variant="subtitle2">
                            <Box display="flex" sx={{ backgroundColor: 'primary' }}>
                                Expenses
                            </Box>
                        </Typography>
                    } />
                    <Tab onClick={() => handleChange(1)} sx={{ alignItems: "start" }} label={
                        <Typography variant="subtitle2">
                            <Box display="flex" sx={{ backgroundColor: 'primary' }}>
                                Income
                            </Box>
                        </Typography>
                    } />
                </Tabs>
                <TabPanel value={value} index={0} height={200}>
                    <Paper square elevation={0} sx={{ m: 0, mt: 1, height: '100%', width: 'inherit', overflow: 'hidden' }}>
                        <Box display="flex" flexDirection="column">
                            <Paper sx={{ p: 1, minHeight: 100 }}>
                                <Grid container>
                                    <Grid item xs={8} className={classes.rowsTitleFirst}>
                                        Expense
                                    </Grid>
                                    <Grid item xs={4} className={classes.rowsTitle}>
                                        Amount
                                    </Grid>
                                    {
                                        cashflow.expense_details.map((data, index) => {
                                            totalExpense += parseFloat(data.amount);
                                            return (
                                                <React.Fragment key={`expense-${index}`}>
                                                    <Grid item xs={8} className={classes.rowsContent} sx={{ borderLeft: "1px solid #000 !important", textAlign: "left !important" }}>
                                                        {
                                                            data.expense
                                                        }
                                                    </Grid>
                                                    <Grid item xs={4} className={classes.rowsContent}>
                                                        {
                                                            data.amount
                                                        }
                                                    </Grid>
                                                </React.Fragment>
                                            )
                                        })
                                    }
                                </Grid>
                            </Paper>
                        </Box>
                    </Paper>
                </TabPanel>
                <TabPanel value={value} index={1} height={200}>
                    <Paper square elevation={0} sx={{ m: 0, mt: 1, height: '100%', width: 'inherit', overflow: 'hidden' }}>
                        <Box display="flex" flexDirection="column" sx={{ mt: 2 }}>
                            <Paper sx={{ p: 1, minHeight: 100 }}>
                                <Grid container>
                                    <Grid item xs={8} className={classes.rowsTitleFirst}>
                                        Income
                                    </Grid>
                                    <Grid item xs={4} className={classes.rowsTitle}>
                                        Amount
                                    </Grid>
                                    {
                                        cashflow.income_details.map((data, index) => {
                                            totalIncome += parseFloat(data.amount);
                                            return (
                                                <React.Fragment key={`expense-${index}`}>
                                                    <Grid item xs={8} className={classes.rowsContent} sx={{ borderLeft: "1px solid #000 !important", textAlign: "left !important" }}>
                                                        {
                                                            data.income
                                                        }
                                                    </Grid>
                                                    <Grid item xs={4} className={classes.rowsContent}>
                                                        {
                                                            data.amount
                                                        }
                                                    </Grid>
                                                </React.Fragment>
                                            )
                                        })
                                    }
                                </Grid>
                            </Paper>
                        </Box>
                    </Paper>
                </TabPanel>
                <Divider sx={{ mb: 1 }} />
                <Box display="flex" flexDirection="row">
                    <Paper sx={{ p: 2, width: '100%' }} >
                        <Box display="flex" flexDirection="row">
                            <Typography variant='h6' fontWeight="bolder" sx={{ mr: 2 }}>Net Income: </Typography>
                            <Typography variant='h6'>{`PHP ${utilityService.numberWithCommas(parseFloat(totalIncome - totalExpense).toFixed(2))}`}</Typography>
                        </Box>
                    </Paper>
                </Box>
            </Paper>
        </div>
    )
}
