import React from 'react';
// MUI Components
import TextField from '@mui/material/TextField';
import Box from '@mui/system/Box';
import Button from '@mui/material/Button';
import {
  GridToolbarColumnsButton,
  GridToolbarDensitySelector,
  GridToolbarFilterButton,
} from '@mui/x-data-grid';
// Page Components
import ExportOptionsMenu from '../forms/fields/ExportOptionsMenu';
// MUI Styling
import { createTheme } from '@mui/material/styles';
import { createStyles, makeStyles } from '@mui/styles';
// react-icons
import { FaCog, FaPlus, FaPrint, FaSearch } from 'react-icons/fa';


const defaultTheme = createTheme();

const useStyles = makeStyles(
  (theme) =>
    createStyles({
      root: {
        padding: theme.spacing(0.5, 0.5, 0),
        justifyContent: 'space-between',
        display: 'flex',
        alignItems: 'flex-start',
        flexWrap: 'wrap',
      }
    }),
  { defaultTheme },
);

const Toolbar = (props) => {

  const [settingsShow, setSettingsShow] = React.useState(false);
  const toggleSettings = () => setSettingsShow(!settingsShow);
  const classes = useStyles();

  const showExport = props.showExport ?? true;
  const showPrint = props.showPrint ?? false;
  const showCreate = props.showCreate ?? false;
  const showSearch = props.showSearch ?? false;

  return (
    <div className={classes.root}>
      <Box sx={{ margin: 1, display: 'flex', fontWeight: 0 }}>
        <Button size="medium" sx={{ color: "#000" }} onClick={toggleSettings}>
          <FaCog style={{ marginRight: 5 }} /> {props.optionsTitle ?? "Settings"}
        </Button>
        {settingsShow &&
          (<div style={{ marginTop: 2 }}>
            <GridToolbarColumnsButton style={{ color: "#000" }} />
            <GridToolbarFilterButton style={{ color: "#000" }} />
            <GridToolbarDensitySelector style={{ color: "#000" }} />
          </div>)
        }
        {showExport &&
          <ExportOptionsMenu />
        }

        {showCreate &&
          <Button
            disableFocusRipple
            disableRipple
            sx={{
              color: "#000",
              fontWeight: 0
            }}
            onClick={() => props.createEvent()}
          >

            <FaPlus style={{ marginRight: 8 }} />
            {props.createTitle ?? "Create"}
          </Button>
        }

        {showPrint &&
          <Button
            disableFocusRipple
            disableRipple
            sx={{
              color: "#000",
              fontWeight: 0
            }}>
            <FaPrint style={{ marginRight: 8 }} />
            Print
          </Button>
        }
      </Box>
      {showSearch &&
        <Box sx={{ m: 0 }}>
          <TextField
            type="search"
            variant="outlined"
            size="small"
            onChange={(e) => props.onChange(e)}
            onKeyUpCapture={(e) => props.onKeyup ? props.onKeyup(e) : null}
            placeholder={props.searchPlaceholder ?? "Search"}
            InputProps={{
              startAdornment: <FaSearch style={{ marginRight: 5 }} />,
            }}
            sx={{
              padding: 1,
              minWidth: props.minWidth ?? 450
            }}
          />
        </Box>
      }
    </div>
  );
}

export default Toolbar;