import React from 'react';
import { format } from 'date-fns';
// Utilities 
import utilityService from '../../../../services/utils/utilityServices';
// Redux
import { useSelector, useDispatch } from 'react-redux';
// MUI Components
import { DataGrid } from '@mui/x-data-grid';
// Page Components
import GridLoadingOverlay from '../../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../../grid/GridNoRowOverlay';
import { getMemberLoanPayments } from '../../../../redux/async/loanPaymentsAsync'; 


const LoadingOverlayOverride = () => {
  return (
    <GridLoadingOverlay />
  );
}

const NoRowOverlayOverride = () => {
  return (
    <GridNoRowOverlay />
  );
}


export default function LoanPaymentRecordsDataTable(props) {

  const dispatch = useDispatch();
  const loanPaymentsRedux = useSelector((state) => state.loanPayments.value);
  const loansRedux = useSelector((state) => state.loans.value);

  const loan_code = loansRedux?.selectedLoan?.loan_code;

  const startDate = loanPaymentsRedux?.startDate;
  const endDate = loanPaymentsRedux?.endDate;
  const verified = loanPaymentsRedux?.verified;
  const loading = loanPaymentsRedux.loading;
 
  const columns = [
    {
      field: 'date_paid', headerName: 'Payment Date', width: 150, renderCell: (params) => {
        return (
          <div>
            <span>
              {params.row.date_paid === null ? "..." : format(new Date(params.row.date_paid), "MM/dd/yyyy")}
            </span>
          </div>
        );
      }
    },
    {
      field: 'amount', headerName: 'Amount Paid', width: 220, renderCell: (params) => {
        return (
          <div>
            <span>
              {params.row.amount === null ? "..." : `PHP ${utilityService.numberWithCommas(utilityService.toFixed(params.row.amount, 2))}`}
            </span>
          </div>
        );
      }
    },
    {
      field: 'payment_due', headerName: 'Payment Due', width: 220, renderCell: (params) => {
        return (
          <div>
            <span>
              {params.row.payment_due === null ? "..." : `PHP ${utilityService.numberWithCommas(utilityService.toFixed(params.row.payment_due, 2))}`}
            </span>
          </div>
        );
      }
    },
    {
      field: 'penalty', headerName: 'Penalty', width: 150, renderCell: (params) => {
        return (
          <div>
            <span>
              {params.row.penalty === null ? "..." : `PHP ${utilityService.numberWithCommas(utilityService.toFixed(params.row.penalty, 2))}`}
            </span>
          </div>
        );
      }
    },
    {
      field: 'payment_type', headerName: 'Payment Type', width: 150, renderCell: (params) => {
        return (
          <div>
            <span>
              {params.row?.payment_type === null ? "..." : utilityService.capitalizeFirstLetter(params.row?.payment_type)}
            </span>
          </div>
        );
      }
    },
    {
      field: 'is_verified', headerName: 'Verified', headerAlign: 'center', align: 'center', flex: true, renderCell: (params) => {
        let status = 'No';
        let color = "#FFA117";

        if (params.row.is_verified === 1) {
          status = "Yes";
          color = "#00BFA5";
        }

        return (
          <div style={{
            borderWidth: 2,
            borderStyle: 'solid',
            borderRadius: 7,
            borderColor: color,
            backgroundColor: color,
            color: "#FFFFFF",
            height: 25,
            width: '70%',
            textAlign: 'center',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }} >
            {status}
          </div >
        );
      }
    }
  ];

  const loadMemberLoanPayments = React.useCallback(() => {

    if (loan_code === undefined || loan_code === null) {
      return false;
    }

    let data = {
      loan_code: loan_code,
      startDate: startDate,
      endDate: endDate,
      verified: verified
    }

    dispatch(getMemberLoanPayments(data));

  }, [dispatch, startDate, endDate, verified, loan_code])

  React.useEffect(() => {
    loadMemberLoanPayments();
  }, [loadMemberLoanPayments, dispatch]);

  return (
    <React.Fragment >     
      <div style={{ height: 450, width: '100%' }}>
        <DataGrid
          rows={loanPaymentsRedux.loanPayments}
          columns={columns} 
          loading={loading}
          density="standard"
          disableSelectionOnClick
          disableColumnMenu
          components={{
            LoadingOverlay: LoadingOverlayOverride,
            NoRowsOverlay: NoRowOverlayOverride, 
          }}
        />
      </div>
    </React.Fragment>
  );
}