import * as React from 'react';
import Button from '@mui/material/Button'; 
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';
// react-icons
import { TiExport } from 'react-icons/ti';
import { GrDocumentCsv, GrDocumentPdf } from 'react-icons/gr';

export default function AccountMenu() {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    return (
        <React.Fragment>
            <Button size='small' sx={{ color: '#000', fontSize: 10 }} onClick={handleClick}>
                <TiExport size={15} style={{ marginRight: 5 }} />
                <Typography variant='span' fontSize={13.9}>Export</Typography>
            </Button>
            <Menu
                anchorEl={anchorEl}
                id="account-menu"
                open={open}
                onClose={handleClose}
                onClick={handleClose}
                PaperProps={{
                    elevation: 0,
                    sx: {
                        overflow: 'visible',
                        filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                        mt: 1.5,
                        '& .MuiAvatar-root': {
                            width: 32,
                            height: 32,
                            ml: 1,
                            mr: 2,
                        },
                        '&:before': {
                            content: '""',
                            display: 'block',
                            position: 'absolute',
                            top: 0,
                            left: 14,
                            width: 10,
                            height: 10,
                            bgcolor: 'background.paper',
                            transform: 'translateY(-50%) rotate(45deg)',
                            zIndex: 0,
                        },
                    },
                }}
                transformOrigin={{ horizontal: 'left', vertical: 'top' }}
                anchorOrigin={{ horizontal: 'left', vertical: 'bottom' }}
            >
                <MenuItem sx={{ minWidth: 150 }}>
                    <GrDocumentCsv style={{ marginRight: 7 }} /> CSV
                </MenuItem>
                <MenuItem>
                    <GrDocumentPdf style={{ marginRight: 7 }} />  PDF
                </MenuItem>
            </Menu>
        </React.Fragment>
    );
}
