import React from 'react'
import LoanDetailsDialog from '../shared/Dialog/LoanDetailsDialog';
import LoanDetailsTabs from '../tabs/LoanDetailsTabs';

export default function LoanDetailsModal(props) {
    return (
        <div>
            <LoanDetailsDialog open={props.open} toggle={props.toggle} title="Loan Details">
                <LoanDetailsTabs toggle={props.toggle} />
            </LoanDetailsDialog>
        </div>
    )
}
