import React from 'react';
import { format } from 'date-fns';
import { useSelector } from 'react-redux';
import utilityServices from '../../../../services/utils/utilityServices';
import { Button, Grid, IconButton, Paper, Typography, TextField } from '@mui/material';
import { FaPrint } from 'react-icons/fa';
import config from '../../../../config/config';

export default function BasicLoanDetailsPage(props) {

    const loansRedux = useSelector((state) => state.loans.value);
    const loan = loansRedux?.selectedLoan;

    const handlePrint = () => {
        let path = `${config.API.dev}reports/loan-details/${loan.loan_code}`;
        window.open(path, '_blank');
    }

    return (
        <div>
            <Paper square sx={{ p: 2, m: 1, ml: 0, border: "1px solid #00BFA5", minHeight: props.minHeight ?? 250 }}>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <TextField
                            variant='standard'
                            label="Purpose of Loan"
                            value={loan?.data?.purpose}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <TextField
                            variant='standard'
                            label="Loan Code"
                            value={loan?.loan_code}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            variant='standard'
                            label="Date Applied"
                            value={format(new Date(loan?.date_applied), "MMM dd, yyyy")}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                        />
                    </Grid>

                    <Grid item xs={4}>
                        <TextField
                            variant='standard'
                            label="Loan Type"
                            value={loan?.data?.type?.type}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            variant='standard'
                            label="Length of Payment"
                            value={loan?.payment_length}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            variant='standard'
                            label="Loan Amount"
                            value={utilityServices.numberWithCommas(parseFloat(loan?.amount_applied).toFixed(2))}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                            InputProps={{
                                startAdornment: (
                                    <IconButton sx={{ m: 0, p: 0, pr: 1 }} disableFocusRipple disableRipple disableTouchRipple>
                                        <Typography>
                                            PHP
                                        </Typography>
                                    </IconButton>
                                )
                            }}
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            variant='standard'
                            label="Interest"
                            value={loan?.interest_rate}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                            InputProps={{
                                endAdornment: (
                                    <IconButton sx={{ m: 0, p: 0, pr: 1 }} disableFocusRipple disableRipple disableTouchRipple>
                                        <Typography>
                                            %
                                        </Typography>
                                    </IconButton>
                                )
                            }}
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            variant='standard'
                            label="Monthly Amortization"
                            value={utilityServices.numberWithCommas(parseFloat(loan?.data?.monthly_amortization).toFixed(2))}
                            size="small"
                            sx={{ m: 0.5 }}
                            inputProps={{
                                readOnly: true
                            }}
                            fullWidth
                            InputProps={{
                                startAdornment: (
                                    <IconButton sx={{ m: 0, p: 0, pr: 1 }} disableFocusRipple disableRipple disableTouchRipple>
                                        <Typography>
                                            PHP
                                        </Typography>
                                    </IconButton>
                                )
                            }}
                        />
                    </Grid>
                </Grid>
            </Paper>
            {props.showPrint !== false &&
                <Button variant="contained" onClick={() => handlePrint()}>
                    <FaPrint style={{ marginRight: 5 }} />
                    Print
                </Button>
            }

        </div>
    )
}
