import React from 'react'
// MUI Components
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
// Page Components
import NoData from '../../shared/NoData/NoData2';
// react-icons
import { FaSearch } from 'react-icons/fa';
import FindMemberModal from '../../modals/FindMemberModal';

export default function NoSelectedMemberPage(props) {

    const [modal, setModal] = React.useState(false);
    const toggleModal = () => setModal(!modal);

    return (
        <React.Fragment>
            {modal && <FindMemberModal open={modal} toggle={toggleModal} />}
            <Box display='flex' flexDirection='column' alignItems="center" sx={{ backgroundColor: "#eee", height: '100%' }}>
                <NoData title="No Member Selected" iconColor="#ccc" />
                <Button
                    size='small'
                    variant='contained'
                    disableRipple
                    disableFocusRipple
                    sx={{
                        mt: 4,
                        minWidth: '20%',
                        maxWidth: '25%',
                        "&:hover": {
                            backgroundColor: "transparent"
                        }
                    }}
                    onClick={() => toggleModal()}
                >
                    <Typography variant="h6">.
                        <FaSearch style={{ marginRight: 5 }} /> Find A Member
                    </Typography>
                </Button>
            </Box>
        </React.Fragment>
    );
}
