import React from 'react';
import { useNavigate } from 'react-router-dom';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { clearSelectedLoan } from '../../../../redux/slices/loansSlice';
// Services
import config from '../../../../config/config';
import alertService from '../../../../services/alert/alertService';
// MUI Components
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Divider from '@mui/material/Divider';
import Paper from '@mui/material/Paper';
// Page Components  
import NoSelectedMemberPage from './NoSelectedMemberPage';
import FindMemberModal from '../../modals/FindMemberModal';
import LoanPaymentRecordsDataTable from '../../datatable/Loans/LoanPaymentRecordsDataTable';
import MemberLoanPaymentDetailsPage from './MemberLoanPaymentDetailsPage';
// react-icons 
import { FaChevronRight, FaMoneyBillAlt } from 'react-icons/fa';
import { MdRepeat } from 'react-icons/md';

import memberOptions from '../../nav/menu/memberOptions';

export default function MemberPaymentsPage() {

    const navigate = useNavigate();
    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const role = authRedux?.role?.role;
    const memberRedux = useSelector((state) => state.members.value);
    const member = memberRedux?.selectedMember;

    const [modal, setModal] = React.useState(false);
    const toggleModal = () => setModal(!modal);

    let profile = {};
    let avatar_path = null;
    if (member) {
        profile = memberRedux.selectedMember;
        if (profile?.user?.avatar !== null) {
            avatar_path = `${config.DOMAIN}users/${profile?.uuid}/files/avatar/${profile?.user.avatar}`;
        }
    }

    let navOptions = memberOptions;

    const handleNavigation = (path) => {
        navigate(`/${role}/${path}`);
    }

    const handleChangeMember = async () => {
        let response = await alertService.confirm({ title: "Change Member?", text: "This will replace the current selected member" });

        if (response.isConfirmed) {
            toggleModal();
        }
    }

    const handleReset = React.useCallback(() => {
        dispatch(clearSelectedLoan());
    }, [dispatch])

    return (
        <div>
            {modal && <FindMemberModal open={modal} toggle={toggleModal} handleReset={handleReset} />}
            <Box display="flex" flexDirection="row">
                <Box display="flex" flexDirection="column" sx={{ m: 0, p: 0, width: 280, borderRight: '1px solid #eee' }}>
                    <Box display="flex" flexDirection="row" justifyContent="center" sx={{ p: 2, backgroundColor: '#eee' }}>
                        <FaMoneyBillAlt size={20} style={{ marginRight: 5, marginTop: 7 }} />
                        <Typography variant="h6" sx={{ mt: 0.2 }}>
                            Payment
                        </Typography>
                    </Box>
                    <Box display="flex" flexDirection="column" alignContent="center" sx={{ backgroundColor: '#fff', mt: 1 }}>
                        <Box display="flex" flexDirection="row" justifyContent="center">
                            <Avatar src={avatar_path} sx={{ width: '90%', height: '100%', mt: 1 }} variant="rounded" />
                        </Box>
                        <Box display="flex" flexDirection="row" justifyContent="center" sx={{ mt: 2 }}>
                            {member &&
                                <Box display="flex" flexDirection="column" alignItems="center">
                                    <Typography variant="body1" sx={{ ml: 1 }} noWrap>
                                        <b>{`${profile.last_name}, ${profile.first_name}`}</b>
                                    </Typography>
                                    <Button size='small' sx={{ mt: 1 }} onClick={() => handleChangeMember()}><MdRepeat /> Change Member</Button>
                                </Box>
                            }
                        </Box>
                        <Divider sx={{ mt: 2 }} />
                        <Box display="flex" flexDirection="column" justifyContent="flex-start" alignContent="flex-start" sx={{ p: 2, pt: 0 }}>
                            <Paper square sx={{ p: 1, mt: 1, minWidth: '100%' }}>
                                <Typography textAlign="center">
                                    Quick Access
                                </Typography>
                                <Divider sx={{ mt: 1 }} />
                                {navOptions.map((data, index) => {
                                    return (
                                        <Button
                                            key={`menu-${index}`}
                                            sx={{
                                                mt: 1,
                                                minWidth: 200,
                                                borderRadius: 0,
                                                justifyContent: "flex-start",
                                                "&:hover": {
                                                    color: '#FFF',
                                                    backgroundColor: "#00BFA5"
                                                }
                                            }}
                                            variant="text"
                                            component="label"
                                            size="small"
                                            onClick={() => handleNavigation(data.path)}
                                        >
                                            {data.icon}
                                            {data.label}
                                        </Button>
                                    );
                                })}
                            </Paper>
                        </Box>
                    </Box>
                </Box>
                <Box display="flex" flexDirection="column" sx={{ width: '100%', p: 1 }}>
                    {
                        !member && <React.Fragment>
                            <Box display="flex" flexDirection="row" sx={{ width: '100%', backgroundColor: '#fff', mt: 1 }}>
                                <Box display="flex" flexDirection="column" flexGrow={1} sx={{ minHeight: '100vh' }}>
                                    <Paper square sx={{
                                        p: 2,
                                        m: 0,
                                        width: '100%',
                                        height: '90%',
                                    }}>
                                        <NoSelectedMemberPage />
                                    </Paper>
                                </Box>
                            </Box>
                        </React.Fragment>
                    }
                    {
                        member && <React.Fragment>
                            <Box display="flex" flexDirection="row" sx={{ backgroundColor: '#eee', p: 1, minHeight: 55 }}>
                                <Paper sx={{ p: 1, borderRadius: 0, width: '100%' }}>
                                    <MemberLoanPaymentDetailsPage />
                                </Paper>
                            </Box>
                            <Divider />
                            <Box display="flex" flexDirection="row" sx={{ width: '100%', backgroundColor: '#fff', mt: 1 }}>
                                <Box display="flex" flexDirection="column" flexGrow={1} sx={{ minHeight: '100vh' }}>
                                    <Paper sx={{ p: 2, m: 0, borderRadius: 0, width: '100%' }}>
                                        <LoanPaymentRecordsDataTable />
                                    </Paper>
                                </Box>
                            </Box>
                        </React.Fragment>
                    }
                </Box>
            </Box >
        </div >
    )
}
