import React from 'react';
import { useNavigate } from 'react-router-dom';
import { format } from 'date-fns';
// Services
import config from '../../../../config/config';
import utilityService from '../../../../services/utils/utilityServices';
import toastService from '../../../../services/toast/toastService';
import membersRepository from '../../../../repositories/membersRepository';
// redux
import { useDispatch, useSelector } from 'react-redux';
// MUI Components
import { Box, Grid, Avatar, TextField, Paper, Typography } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { setMemberProfile } from '../../../../redux/slices/membersSlice';
// react-icons   

export default function BasicProfilePage() {

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const loansRedux = useSelector((state) => state.loans.value);
    const meRedux = useSelector((state) => state.auth.value).me;

    const role = meRedux?.role?.role;

    let member = loansRedux?.selectedLoan?.data?.member ?? null;
    let avatar_path, presentAddress, middle_name = null;

    const [loading, setLoading] = React.useState(false);

    if (member) {
        if (member?.user.avatar !== null && member?.user.avatar !== undefined) {
            avatar_path = `${config.DOMAIN}users/${member?.uuid}/files/avatar/${member?.user.avatar}`;
        }

        if (member?.middle_name !== "") {
            middle_name = `${member?.middle_name[0]?.toUpperCase()}.`;
        }

        if (member?.address.length > 0) { 
            let add = member?.address[0];
            
            if (member?.address[0].address_type.toLowerCase() !== "present") {
                let add = member?.address[1];                
            }

            presentAddress = `${add?.streetpurok}, ${add?.barangay}, ${add?.zipcode?.area}, ${add?.zipcode?.province}`
        }
    }

    const handleNavigation = async () => {

        try {
            setLoading(true);
            let response = await membersRepository.getMember(member?.uuid);
            dispatch(setMemberProfile(response));
            navigate(`/${role}/member/profile`);
        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
        }

    }

    return (
        <div>
            <Paper square sx={{ p: 1, pb: 2, border: "1px solid #00BFA5" }}>
                <Box display="flex" flexDirection="row">
                    <Box display="flex" flexDirection="column" sx={{ m: 0, p: 0, width: 230 }}>
                        <Box display="flex" flexDirection="column" alignContent="center" sx={{ backgroundColor: '#fff' }}>
                            <Box display="flex" flexDirection="row" justifyContent="center">
                                <Avatar src={avatar_path} sx={{ width: '100%', height: '100%' }} variant="rounded" />
                            </Box>
                        </Box>
                    </Box>
                    <Box display="flex" flexDirection="column" sx={{ width: '100%', p: 2 }}>
                        <Box display="flex" flexDirection="row" sx={{ width: '100%', backgroundColor: '#fff' }}>
                            <Box display="flex" flexDirection="column" flexGrow={1}>
                                <Grid container spacing={1}>
                                    <Grid item xs={6}>
                                        <TextField
                                            label="Member"
                                            variant='standard'
                                            inputProps={{
                                                readOnly: true
                                            }}
                                            value={`${member?.last_name}, ${member?.first_name} ${middle_name}`}
                                            fullWidth
                                        />
                                    </Grid>
                                    <Grid item xs={6} >
                                        <TextField
                                            label="Email"
                                            variant='standard'
                                            inputProps={{
                                                readOnly: true
                                            }}
                                            value={member?.user?.email ?? '...'}
                                            fullWidth
                                        />
                                    </Grid>
                                    <Grid item xs={12}>
                                        <TextField
                                            label="Address"
                                            variant='standard'
                                            inputProps={{
                                                readOnly: true
                                            }}
                                            value={presentAddress}
                                            fullWidth
                                        />
                                    </Grid>
                                    <Grid item xs={6}>
                                        <TextField
                                            label="Birthdate"
                                            variant='standard'
                                            inputProps={{
                                                readOnly: true
                                            }}
                                            value={format(new Date(member?.birthdate), "MMMM dd, yyyy") ?? '...'}
                                            fullWidth
                                        />
                                    </Grid>
                                    <Grid item xs={6}>
                                        <TextField
                                            label="Gender"
                                            variant='standard'
                                            inputProps={{
                                                readOnly: true
                                            }}
                                            value={(member?.gender === "m" ? "Male" : "Female") ?? '...'}
                                            fullWidth
                                        />
                                    </Grid>
                                    <Grid item xs={6}>
                                        <TextField
                                            label="Civil Status"
                                            variant='standard'
                                            inputProps={{
                                                readOnly: true
                                            }}
                                            value={utilityService.capitalizeFirstLetter(member?.civil_status) ?? '...'}
                                            fullWidth
                                        />
                                    </Grid>
                                    <Grid item xs={6}>
                                        <TextField
                                            label="Nationality"
                                            variant='standard'
                                            inputProps={{
                                                readOnly: true
                                            }}
                                            value={member?.nationality ?? '...'}
                                            fullWidth
                                        />
                                    </Grid>
                                </Grid>
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Paper>
            <Box display="flex" flexDirection="row" justifyContent="flex-end">
                <LoadingButton
                    loading={loading}
                    onClick={() => handleNavigation()}
                    sx={{
                        mt: 1,
                        "&:hover": {
                            backgroundColor: 'transparent'
                        }
                    }}
                >
                    See Full Profile...
                </LoadingButton>
            </Box>
        </div >
    )
}
