import * as React from 'react';
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import TimelineDot from '@mui/lab/TimelineDot';
import Typography from '@mui/material/Typography';
import { FaCheck } from 'react-icons/fa';
import { format } from 'date-fns';

export default function ActivityTimeline(props) {

    const items = props?.items ?? [];

    return (
        <Timeline position="alternate">
            {items.map((data, index) => {
                return <TimelineItem key={`timeline-${index}`}>
                    <TimelineOppositeContent
                        sx={{ m: 'auto 0' }}
                        align="right"
                        variant="body2"
                        color="secondary"
                    >
                        {format(new Date(data.datetime), 'MM/d/yyyy')}
                        <Typography>{format(new Date(data.datetime), 'h:ii:ss a')}</Typography>
                    </TimelineOppositeContent>
                    <TimelineSeparator>
                        {index !== 0 &&
                            <TimelineConnector />
                        }
                        <TimelineDot color='primary'>
                            <FaCheck size={12} color="#FFF" />
                        </TimelineDot>
                        {index === items.length - 1 && index === 0 &&
                            <TimelineConnector />
                        }
                    </TimelineSeparator>
                    <TimelineContent sx={{ py: '12px', px: 2 }}>
                        <Typography variant="body1" component="span">
                            {data.activity}
                        </Typography>
                    </TimelineContent>
                </TimelineItem>
            })}
        </Timeline>
    );
}
