import React from 'react';
// Utilities  
import config from '../../../../config/config';
import utilityService from '../../../../services/utils/utilityServices';
import toastService from '../../../../services/toast/toastService';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { get, searchMember } from '../../../../redux/async/membersAsync';
import { getDepositAccounts, getWithdrawAccounts } from '../../../../redux/async/memberAccountsAsync';
import { selectedMember } from '../../../../redux/slices/membersSlice';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
// Page Components
import GridLoadingOverlay from '../../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../../grid/GridNoRowOverlay';
import Toolbar from '../../grid/Toolbar';
// react-icons
import { FaLightbulb, FaUser } from 'react-icons/fa';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = (props) => {
    return (
        <GridNoRowOverlay text={props.text} />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            onChange={props.onChange}
            onKeyup={props.onKeyup}
            optionsTitle={"Options"}
            searchPlaceholder={"Type member last or first name and press enter..."}
            showSearch={true}
            showExport={false}
        />
    );
}

export default function FindMemberDataTable(props) {

    const dispatch = useDispatch();
    const membersRedux = useSelector((state) => state.members.value);
    const loading = membersRedux.loading;

    const rowData = membersRedux.members;

    const [keyword, setKeyword] = React.useState("");

    const width = 200;
    const columns = [
        {
            field: 'avatar', headerName: 'Avatar', width: 150, headerAlign: 'center', align: 'center', sortable: false, renderCell: (params) => {

                let data = params.row;
                let avatar_path = null;

                if (data?.avatar) {
                    avatar_path = `${config.DOMAIN}users/${data.uuid}/files/avatar/${data.avatar}`
                }

                return (
                    <div>
                        <Avatar src={avatar_path} />
                    </div>
                );
            }
        },
        {
            field: 'last_name', headerName: 'Full Name', flex: true, renderCell: (params) => {
                return (
                    <div>
                        {utilityService.capitalizeFirstLetter(params.row.last_name)}, {utilityService.capitalizeFirstLetter(params.row.first_name)}
                    </div>
                )
            }
        },
        { field: 'branch', headerName: 'Branch', width: width },
        { field: 'contact_no', headerName: 'Contact #', width: width },
        { field: 'fb_link', headerName: 'Social Media', width: width, hide: true },
        {
            field: 'actions', headerName: 'Options', width: width, type: 'actions', getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaUser size={18} />}
                    onClick={() => handleSelectedMember(params.row)}
                    label="Select Member"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
            ]
        },
    ];

    const handleSelectedMember = (data) => { 

        if (props.resetAccount) {
            props.resetAccount();
        }

        dispatch(selectedMember(data));
        dispatch(getDepositAccounts({ uuid: data?.uuid }));
        dispatch(getWithdrawAccounts({ uuid: data?.uuid }));
        
        if (props.toggle) {
            props.toggle();
        }
    }

    const handleSearchText = (e) => {
        let keywords = e.target.value;
        setKeyword(keywords);
        if (keywords === "") {
            loadMembers();
        }
    }

    const handleSearch = (e) => {
        if (e.key === "Enter") {
            if (utilityService.removeSpaces(keyword) !== "") {
                dispatch(searchMember({ keyword }));
            } else {
                toastService.alert("Please enter a keyword");
            }
        }
    }

    const loadMembers = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {

        let mounted = false;

        if (!mounted) {
            loadMembers();
        }

        return () => mounted = true;

    }, [loadMembers]);

    return (
        <div style={{ height: 200, width: '100%' }}>
            <Typography variant="subtitle2" sx={{ mb: 1 }}>
                <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                Tips: You can double-click a row to select or use the option
            </Typography>
            <DataGrid
                style={{ height: 450 }}
                rows={rowData.length <= 0 ? membersRedux.members : rowData}
                columns={columns}
                onCellDoubleClick={(params) => handleSelectedMember(params.row)}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                    Toolbar: ToolbarOverride
                }}
                componentsProps={{
                    toolbar: {
                        onChange: (e) => handleSearchText(e),
                        onKeyup: (e) => handleSearch(e),
                    },
                    noRowsOverlay: {
                        text: "No records found"
                    }
                }}
            />
        </div>
    );
}