import React from 'react';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { getMemberReleaseLoans } from '../../../../redux/async/loansAsync';
import { setSelectedLoan, clearSelectedLoan, setLoanPaymentSchedules, setSelectedLoanIndex } from '../../../../redux/slices/loansSlice';
// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
// Components
import LoanDetailsModal from '../../modals/LoanDetailsModal';
// react-icons 
import { FaRedoAlt, FaInfoCircle } from 'react-icons/fa';
import { setLoanPayments } from '../../../../redux/slices/loanPaymentSlice';

const MemberLoans = (props) => {

    const dispatch = useDispatch();

    const loansRedux = useSelector((state) => state.loans.value);
    const memberRedux = useSelector((state) => state.members.value);
    const uuid = memberRedux?.selectedMember?.uuid;
    const loanIndex = loansRedux?.selectedLoanIndex;

    const [loanDetailsModal, setLoanDetailsModal] = React.useState(false);
    const toggleShowLoanDetailsModal = () => setLoanDetailsModal(!loanDetailsModal);

    let memberLoans = [];
    let loans = [{ value: "", label: "No Loans Released Found" }];

    if (loansRedux?.releasedLoans?.length > 0) {

        memberLoans = loansRedux?.releasedLoans;

        loans = memberLoans.map((data, index) => {
            return {
                value: index,
                label: data.loan_code,
                data: data
            }
        });
    }


    const handleChange = (value) => {

        if (value === "") {
            return false;
        }

        dispatch(setSelectedLoanIndex(value));

        let loanSelected = memberLoans[value];
        dispatch(setSelectedLoan(loanSelected));
        dispatch(setLoanPaymentSchedules(loanSelected.data));
    }

    const handleLoanDetails = () => {
        toggleShowLoanDetailsModal();
    }

    const handleReset = () => {
        dispatch(setSelectedLoanIndex(''));
        dispatch(clearSelectedLoan());
        dispatch(setLoanPayments([]));
    }

    const loadLoans = React.useCallback(async () => {
        await dispatch(getMemberReleaseLoans({ uuid: uuid })).unwrap();
    }, [dispatch, uuid]);

    React.useEffect(() => {

        let mounted = false;

        if (!mounted) {

            async function fetchData() {
                await loadLoans();
            }

            fetchData();
            let loan = memberLoans[loanIndex];
            dispatch(setSelectedLoan(loan));
            dispatch(setLoanPaymentSchedules(loan?.data));
        }

        return () => {
            mounted = true;
        }

    }, [dispatch, loadLoans, uuid, props.loan, loanIndex])

    return (
        <Box display="flex" flexDirection="row" sx={{ maxHeight: 45, minHeight: 45 }}>
            {loanDetailsModal && <LoanDetailsModal open={loanDetailsModal} toggle={toggleShowLoanDetailsModal} />}
            <React.Fragment>
                <Tooltip placement='top' title="Click to reset selected loan" arrow>
                    <Button disableRipple sx={{ p: 0, m: 0, pr: 1, pl: 1, minWidth: 0, '&:hover': { backgroundColor: 'transparent' } }} onClick={() => handleReset()}>
                        <FaRedoAlt size={25} />
                    </Button>
                </Tooltip>
                <TextField
                    select
                    label='Loans'
                    name="loans"
                    size="small"
                    variant={props.variant !== null ? props.variant : "outlined"}
                    value={loanIndex}
                    onChange={(e) => handleChange(e.target.value)}
                    fullWidth={props.fullWidth ?? true}
                    required={!props.isEdit ?? true}
                    disabled={loans.loading}
                    inputProps={
                        {
                            readOnly: props.isEdit ?? false,
                            MenuProps: { disableScrollLock: true },
                        }
                    }
                    sx={{ minWidth: props.minWidth ?? 500 }}
                > 
                    {loans.map((option, index) => {
                        return <MenuItem key={`loans-${index}`} value={option.value}>
                            {option.label}
                        </MenuItem>
                    })}
                </TextField>
                {loanIndex !== '' &&
                    <Tooltip placement='top' title="Click to view loan info" arrow>
                        <Button
                            disableRipple
                            sx={{ p: 0, m: 0, pl: 1, minWidth: 0, '&:hover': { backgroundColor: 'transparent' } }}
                            onClick={handleLoanDetails}
                        >
                            <FaInfoCircle size={30} style={{ marginRight: 5 }} />
                        </Button>
                    </Tooltip>
                }
            </React.Fragment>
        </Box>
    )
}

export default MemberLoans;