import {
    FaFileContract,
    FaUser,
    FaUniversity,
    FaMoneyBillAlt,
} from "react-icons/fa";

const menu = [
    {
        "label": "Profile",
        "path": "member/profile",
        "icon": <FaUser size={20} style={{ marginRight: 5 }} />
    },
    {
        "label": "Accounts",
        "path": "member/accounts",
        "icon": <FaUniversity size={20} style={{ marginRight: 5 }} />
    },
    {
        "label": "Loans",
        "path": "member/loans",
        "icon": <FaFileContract size={20} style={{ marginRight: 5 }} />
    },
    {
        "label": "Payments",
        "path": "member/payments",
        "icon": <FaMoneyBillAlt size={20} style={{ marginRight: 5 }} />
    }
];

export default menu;