import React from 'react';
// Redux
import { useSelector } from 'react-redux';
// MUI Components
import { Box, Grid, Link, Paper, Typography } from '@mui/material'
import { makeStyles } from '@mui/styles'
import config from '../../../../config/config';
import { FaCheck } from 'react-icons/fa';


const useStyles = makeStyles({
    rowsTitleFirst: {
        border: '1px solid #000',
        fontWeight: 'bolder',
        textAlign: 'center',
    },
    rowsContentFirst: {
        border: '1px solid #000',
        padding: '5px',
        paddingLeft: '15px'
    },
    rowsTitle: {
        border: '1px solid #000',
        borderLeft: 'none',
        fontWeight: 'bolder',
        textAlign: 'center',
    },
    rowsContent: {
        border: '1px solid #000',
        borderLeft: 'none',
        borderTop: 'none',
        padding: '5px',
        paddingLeft: '15px',
        textAlign: 'center',

    },
})

export default function CollateralDetailsPage(props) {

    const loanRedux = useSelector((state) => state.loans.value);

    const selectedLoan = loanRedux.selectedLoan;
    const classes = useStyles(props);

    const collaterals = selectedLoan.data.collaterals;

    return (
        <div>
            <Paper square sx={{ p: 1, border: "1px solid #00BFA5" }}>
                <Grid container>
                    <Grid item xs={5} className={classes.rowsTitleFirst}>
                        Collateral
                    </Grid>
                    <Grid item xs={5} className={classes.rowsTitle}>
                        Attachments
                    </Grid>
                    <Grid item xs={2} className={classes.rowsTitle}>
                        Status
                    </Grid>
                    {
                        collaterals.map((data, index) => {
                            return (
                                <React.Fragment key={`comaker-${index}`}>
                                    <Grid item xs={5} className={classes.rowsContent} sx={{ borderLeft: "1px solid #000 !important", textAlign: "left !important" }}>
                                        {
                                            data.collateral
                                        }
                                    </Grid>
                                    <Grid item xs={5} className={classes.rowsContent}>
                                        <Link key={`link-${index}`} href={`${config.DOMAIN}users/${data?.file_path}/${data?.file_name}`} target="_blank">
                                            Click to preview attachment
                                        </Link>
                                    </Grid>
                                    <Grid item xs={2} className={classes.rowsContent}>
                                        {
                                            data.is_verified === 1 ?
                                                <Typography color="primary">
                                                    Verified
                                                    <FaCheck size={12} style={{ marginLeft: 5 }} />
                                                </Typography>
                                                :
                                                <Typography color="secondary">
                                                    Unverified
                                                </Typography>
                                        }
                                    </Grid>
                                </React.Fragment>
                            )
                        })
                    }
                </Grid>
            </Paper>
        </div>
    )
}
