import React from 'react';
// MUI Components
import Box from '@mui/material/Box';
// Page Components
import LargeModalDialog from '../shared/Dialog/LargeModalDialog';
import FindMemberDataTable from '../datatable/Members/FindMemberDataTable';

export default function FindMemberModal(props) {
    return (
        <div>
            <LargeModalDialog open={props.open} toggle={props.toggle} title="Find Member" withClose={props.withClose}>
                <Box sx={{ minHeight: 500 }}>
                    <FindMemberDataTable toggle={props.toggle} resetAccount={props.handleReset} />
                </Box>
            </LargeModalDialog>
        </div>
    )
}
