import * as React from 'react';

// MUI Components 
import {
    Paper,
    Box,
    Tabs,
    Tab,
    Typography
} from '@mui/material';
// Page Components
import TabPanel from './components/TabPanel3';

// options
import loandetailstabs from './options/loandetailstabs';


export default function LoanDetailsTabs(props) {

    const options = loandetailstabs;
    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ maxWidth: '100%', mt: 0, bgcolor: 'background.paper' }}>
            <Tabs
                value={value}
                sx={{ p: 1, pt: 0 }}
            >
                {
                    options.map((data, index) => {
                        return <Tab onClick={() => handleChange(index)} key={`tabkey-${index}`} label={
                            <Typography variant="caption1">
                                <Box display="flex">
                                    {data.icon}
                                    {data.name}
                                </Box>
                            </Typography>
                        } />
                    })
                }
            </Tabs>
            {
                options.map((data, index) => {
                    return <TabPanel value={value} index={index} key={`tabpanelkey-${index}`}>
                        <Paper elevation={1} sx={{ p: 2, pt: 1, width: '100%', height: '100%' }}>
                            {data.content}
                        </Paper>
                    </TabPanel>
                })
            }
        </Box>
    );
}
